@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/latoregular.woff2") format("woff2"), url("../fonts/lato/latoregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}
img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

section {
  display: block;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  display: inline-block;
  margin: 0;
  padding: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover, .button:focus, .button:focus-visible {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button:disabled, .button--disabled {
  color: rgba(255, 255, 255, 0.3);
  background-color: #bababa;
  pointer-events: none;
}

.page-title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}
@media (min-width: 768px) {
  .page-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.title-lines {
  position: relative;
  margin: 0 -20px;
  overflow: hidden;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .title-lines {
    margin: 0 -30px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .title-lines {
    margin: 0 -102px 0 -110px;
    padding-left: 70px;
  }
}

.title-lines span {
  display: inline-block;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .title-lines span {
    padding: 0 40px;
  }
}

.title-lines:before,
.title-lines:after {
  content: "";
  position: absolute;
  top: 16px;
  display: inline-block;
  width: 100%;
  height: 2px;
  vertical-align: middle;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .title-lines:before,
  .title-lines:after {
    top: 23px;
  }
}

.title-lines:before {
  display: none;
  margin-left: -100%;
}
@media (min-width: 768px) {
  .title-lines:before {
    display: inline-block;
  }
}

.title-lines:after {
  margin-right: -100%;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

@media (min-width: 1440px) {
  .header {
    position: relative;
  }
}

@media (min-width: 1440px) {
  .header--home::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("/img/cat-background-desktop.jpg");
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
  }
}
@media (min-width: 1440px) and (min-resolution: 144dpi), (min-width: 1440px) and (min-resolution: 1.5dppx) {
  .header--home::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("/img/cat-background-desktop@2x.jpg");
  }
}

.header__container {
  padding: 0;
}

.main-nav {
  position: relative;
  display: grid;
  min-height: 65px;
  grid-template-columns: repeat(2, auto);
}
@media (min-width: 768px) {
  .main-nav {
    display: grid;
    align-items: start;
    padding: 21px 31px 43px;
    gap: 20px;
    grid-template-columns: auto 1fr;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    padding: 52px 0 20px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.main-nav__list {
  position: absolute;
  top: 100%;
  right: -20px;
  left: -20px;
  z-index: 5;
  display: none;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: 8px 0 0 0;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    margin: 17px 0 0 0;
  }
}

.main-nav.is-open .main-nav__list {
  display: flex;
  min-height: 198px;
  text-align: center;
}

.no-js .main-nav__list {
  position: static;
  display: block;
}

.main-nav__logo {
  display: block;
  width: 191px;
  height: 38px;
  margin-top: 13px;
  margin-left: 19px;
}
@media (min-width: 768px) {
  .main-nav__logo {
    width: 174px;
    height: 50px;
    margin-top: 3px;
    margin-left: -1px;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo {
    width: 202px;
    height: 59px;
    margin-left: -1px;
  }
}
.main-nav__logo:hover, .main-nav__logo:focus {
  outline: none;
  opacity: 0.8;
}
.main-nav__logo:active {
  opacity: 0.6;
}

.main-nav__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.main-nav__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    margin: 0 3px 0 31px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    margin-left: 35px;
  }
}

.main-nav__link {
  display: block;
  padding: 21px 20px 24px;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    position: relative;
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    padding: 2px 0;
    font-size: 20px;
    line-height: 30px;
  }
}
.main-nav__link:hover, .main-nav__link:focus {
  outline: none;
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}

.main-nav__link--current {
  pointer-events: none;
}
@media (min-width: 768px) {
  .main-nav__link--current::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 2px;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__link--current {
    height: 41px;
  }
}

@media (min-width: 1440px) {
  .header--home .main-nav__link--current::before {
    background-color: #ffffff;
  }
}

@media (min-width: 1440px) {
  .header--home .main-nav__link {
    color: #ffffff;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 22px;
  right: 21px;
  z-index: 2;
  display: block;
  width: 24px;
  height: 18px;
  background: none;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav__toggle::before {
  top: 6px;
}
.main-nav__toggle::after {
  bottom: 0;
}

.is-open .main-nav__toggle {
  border-top: 2px solid transparent;
}
.is-open .main-nav__toggle::before {
  left: 4px;
  transform: rotate(-46deg);
}
.is-open .main-nav__toggle::after {
  top: 6px;
  left: 4px;
  transform: rotate(46deg);
}

.no-js .main-nav__toggle {
  display: none;
}

.no-js .main-nav {
  position: static;
}

.is-open .main-nav__list {
  display: flex;
}

